body {
  margin: 0;
}

h2, h3 {
  color: white
}


.login-form {
  margin-top: 50px;
  text-align: center;
  width: 50%;
}

.login-form h2 {
  color:black;
}

.login-form button {
  display: block;
  margin: 10px auto;
}

.container button {
  margin-top: 10px;
  margin-bottom: 10px;
}

.container {
  margin: 50px auto;
  width: 60%;
  padding: 10px;
  box-shadow: 5px 10px 8px #bbbbbb;
}

.nav {
  display: flex;
  background-color: #363332;
  height: 50px;
}

.nav h2 {
  margin: auto 25px;
}

.nav ul {
  list-style-type: none;
}

.nav li {
  display: inline;
}

.nav a {
  padding: 25px;
  text-decoration: none;
  color: white;
}

.nav a:hover {
  color: orangered
}

.nav button {
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

.nav button:hover {
  color: orangered
}

.row-1 {
  margin: 15px auto;
  display: flex;
  justify-content: space-around;
  background-color: #464444;
}

.row-2 {
  margin: 15px auto;
  display: flex;
  justify-content: space-around;
  background-color: #464444;
}

.col {
  text-align: center;
}

.col input {
  display: block;
  margin-top: 25px;
}

table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

td, th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}


tr:nth-child(even) {
  background-color: #dddddd;
}


